
// 导入title
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import VXETable from '@/components/common/Table';
import PageAuthoritySetting from '@/views/main/RoleManagement/PageAuthoritySetting';
import {
  listAcquireTemplates,
  insertAcquireTemplateName,
  updateAcquireTemplateName,
  updateAcquireTemplateProtocolDefinition,
  updateAcquireTemplatePointDataDefinition
} from '@/api/publishConfig.js';
export default {
  data() {
    return {
      showEdit: false,
      selectRow: null,
      submitLoading: false,
      formData: {
        protocolName: '',
        description: ''
      },
      tableLoading: false,
      dialogVisible: false,
      adddialogVisible: false,
      tableData: [

      ],

      // 模态框输入框的宽度
      formLabelWidth: '100px',
      // 模态框表单
      tableData1: [

      ],
      tableData2: [

      ],
      tablePage: {
        total: 10,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: ['PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'Sizes', 'Total'],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      }
    };
  },
  components: {
    HeaderTitle,
    HeadOperationItem,
    VXETable,
    PageAuthoritySetting
  },
  filters: {
    // 时间过滤
    timeSwitch(value) {
      const date = new Date(value);
      const y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      let h = date.getHours();
      h = h < 10 ? ('0' + h) : h;
      let m = date.getMinutes();
      m = m < 10 ? ('0' + m) : m;
      let s = date.getSeconds();
      s = s < 10 ? ('0' + s) : s;
      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
    }
  },
  methods: {
    submitEvent() {
      this.showEdit = true;
      if (this.selectRow) {
        const conditionJson = {
          description: this.formData.description,
          protocolName: this.formData.protocolName,
          id: this.selectRow.id
        };
        updateAcquireTemplateName(conditionJson).then(res => {
          this.$message({
            message: '编辑成功',
            type: 'success'
          });
          Object.assign(this.selectRow, this.formData);
          this.showEdit = false;
        });
      } else {
        insertAcquireTemplateName(this.formData).then(res => {
          this.$message({
            message: '新增成功',
            type: 'success'
          });
          this.$refs.xTable.insert(this.formData);
          this.showEdit = false;
          this.tablePage.total = this.tableData.length;
        });
      }
    },
    // 连接字段模态框添加
    async addEvent(row) {
      const record = {
        name: ''
      };
      const { row: newRow } = await this.$refs.xTable.insert(record, row);
      await this.$refs.xTable.setActiveCell(newRow, 'name');
    },
    // 点表字段添加
    async addEvent1(row) {
      const record = {
        name: ''
      };
      const { row: newRow } = await this.$refs.xTable.insert(record, row);
      await this.$refs.xTable.setActiveCell(newRow, 'name');
    },
    // 连接字段删除
    delEvent(row) {
      this.$message({
        message: '删除成功',
        type: 'warning'
      });
      this.$refs.xTable.remove(row);
    },
    // 点表字段删除
    delEvent1(row) {
      this.$message({
        message: '删除成功',
        type: 'warning'
      });
      this.$refs.xTable.remove(row);
    },
    // 切换页码
    onTablePageChange(currentPage, pageSize) {
      this.tablePage.currentPage = currentPage;
      this.tablePage.pageSize = pageSize;
      this.collectList();
    },
    handlePageChange1({ currentPage, pageSize }) {
      this.tablePage.currentPage = currentPage;
      this.tablePage.pageSize = pageSize;
      this.collectList();
    },
    // 关闭新增模态框
    closeAdd() {
      this.$message({
        message: '关闭新增',
        type: 'warning'
      });
    },
    // 添加连接字段模态框显示
    concat(row) {
      this.selectRow = row;
      this.concatList();
    },
    concat1() {
      this.orderList();
    },

    // 点击连接字段确认按钮触发的方法
    ConcatConfirm() {
      updateAcquireTemplateProtocolDefinition({ id: this.selectRow.id }, this.tableData1).then(res => {
        if (res.code === 0) {
          this.$message({
            message: '保存成功',
            type: 'success'
          });
          this.adddialogVisible = false;
        }
      });
    },
    // 点表字段确认
    orderConfirm() {
      updateAcquireTemplatePointDataDefinition({ id: this.selectRow.id }, this.tableData2).then(res => {
        if (res.code === 0) {
          this.$message({
            message: '保存成功',
            type: 'success'
          });
          this.dialogVisible = false;
        }
      });
    },
    // 点击新增按钮触发的方法
    insertEvent() {
      this.formData = {
        seq: '',
        moduleName: '',
        description: ''
      };
      this.selectRow = null;
      this.showEdit = true;
    },
    // 点击编辑按钮触发的方法
    editEvent(row) {
      this.formData = {
        seq: row.seq,
        moduleName: row.moduleName,
        description: row.description
      };
      this.selectRow = row;
      this.showEdit = true;
    },
    // 点击删除按钮触发的方法
    removeEvent(row) {
      this.$XModal.confirm('您确定要删除该数据?').then(type => {
        if (type === 'confirm') {
          this.$message({
            message: '删除成功',
            type: 'warning'
          });
          this.$refs.xTable.remove(row);
        }
      });
    },
    // 表格列表
    collectList() {
      this.tableLoading = true;
      const conditionJson = {
        pageNum: this.tablePage.currentPage,
        pageSize: this.tablePage.pageSize
      };
      listAcquireTemplates(conditionJson).then(res => {
        console.log(res);
        this.tableLoading = false;
        this.tableData = res.data;
        // this.tableData = res.data.slice((this.tablePage.currentPage - 1) * this.tablePage.pageSize, this.tablePage.currentPage * this.tablePage.pageSize);
      });
    },
    // 连接字段列表
    concatList() {
      const conditionJson = {
        id: this.selectRow.id,
        pageNum: this.tablePage.currentPage,
        pageSize: this.tablePage.pageSize
      };
      listAcquireTemplates(conditionJson).then(res => {
        this.tableData1 = res.data[0].protocolDefinition;
      });
    },
    // 点表字段列表
    orderList() {
      // this.tableLoading = true;
      const conditionJson = {
        id: this.selectRow.id,
        pageNum: this.tablePage.currentPage,
        pageSize: this.tablePage.pageSize
      };
      listAcquireTemplates(conditionJson).then(res => {
        this.tableData2 = res.data[0].protocolDefinition;
      });
    }
  },
  created() {
    this.collectList();
    const conditionJson = {
      id: this.selectRow.id,
      pageNum: this.tablePage.currentPage,
      pageSize: this.tablePage.pageSize
    };
    listAcquireTemplates(conditionJson).then(res => {
      this.tableData1 = res.data[0].protocolDefinition;
    });
  }
};
