<template>
  <div class="network collectModule">
    <div class="header_con">
      <!-- <div class="item_oper" @click="handleReload">
        <i class="icon-refresh"></i>
        <span class="name">刷新</span>
      </div> -->
      <div class="item_oper" @click="insertEvent">
        <i class="icon-add"></i>
        <span class="name">新增</span>
      </div>
    </div>
    <div class="mian_con">
      <vxe-table
        border
        resizable
        highlight-current-row
        show-overflow
        ref="xTable"
        :height="$options.filters.getTableHeight(200)"
        :data="tableData"
        >
        <vxe-table-column type="seq" title="序号" width="60"></vxe-table-column>
        <vxe-table-column field="protocolName" width="300" title="模板名称"></vxe-table-column>
        <vxe-table-column field="description" title="说明" ></vxe-table-column>
        <vxe-table-column field="createdTime" title="创建时间">
          <template v-slot="{row}" >
            <span>{{row.createdTime | timeSwitch }}</span>
          </template>
        </vxe-table-column>
        <vxe-table-column field="updatedTime" title="更新时间" show-overflow>
          <template v-slot="{row}" >
            <span>{{row.updatedTime | timeSwitch }}</span>
          </template>
        </vxe-table-column>
        <vxe-table-column title="操作" width="300" show-overflow>
        <template v-slot="{ row }">
          <vxe-button type="text"  status="primary" @click="editEvent(row)">编辑</vxe-button>
          <vxe-button type="text"  status="danger"  @click="removeEvent(row)" >删除</vxe-button>
          <vxe-button type="text" status="primary"  @click="concat(row,column)"  >连接字段</vxe-button>
          <vxe-button type="text" status="primary"  @click="concat1(row,column)" >点表字段</vxe-button>
        </template>
      </vxe-table-column>
      </vxe-table>
      <!-- 页数 -->
      <div class="page_con">
        <vxe-pager
          :current-page="tablePage.currentPage"
          :page-size="tablePage.pageSize"
          :total="tablePage.total"
          :layouts="tablePage.layouts"
          @page-change="handlePageChange1">
        </vxe-pager>
      </div>
      <vxe-modal ref="xModal" v-model="showEdit" :title="selectRow ? '编辑采集模板' : '新增采集模板'"  width="800"  destroy-on-close>
        <template v-slot>
            <vxe-form  :data="formData" title-align="right" title-width="100" @submit="submitEvent">
            <vxe-form-item title="模板名称：" field="protocolName" span="24" :item-render="{name: 'input'}"></vxe-form-item>
            <vxe-form-item title="说明：" field="description" span="24" :item-render="{name: 'input'}"></vxe-form-item>
            <vxe-form-item :data="formData" align="center" span="24">
              <template v-slot>
                <vxe-button type="submit" status="primary">确定</vxe-button>
                <vxe-button @click="$refs.xModal.close()">取消</vxe-button>
              </template>
            </vxe-form-item>
            </vxe-form>
        </template>
      </vxe-modal>
      <!-- 添加连接字段模态框 -->
      <el-dialog title="连接字段(或点表字段)"
        :visible.sync="adddialogVisible"
        width='50%'
      >
        <div class="main">
          <el-button style="position: absolute;top: 3%;left:24%;width: 70px; height: 30px;" type="primary"  @click="addEvent()">添加</el-button>
          <vxe-table
            :data="tableData1"
            keep-source
            ref="xTable"
            :edit-config="{trigger: 'click', mode: 'cell',icon: 'fa fa-pencil', showStatus: true}">
            <vxe-table-column field="id" type="seq" title="序号" width="60"></vxe-table-column>
            <vxe-table-column field="name" title="名称" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="dataTypeEnum" title="数据类型" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="explaination" title="描述" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
              <vxe-table-column field="remark" title="注释" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="optionList" title="可选项(以英文逗号分割)" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="defaultValue" title="默认值" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="unit" title="单位" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column  field="oper" title="操作">
                <template #default="{ row }">
                  <vxe-button type="text" icon="fa fa-trash-o" status="danger" content="删除" @click="delEvent(row)"></vxe-button>
                </template>
            </vxe-table-column>
          </vxe-table>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button class="custom-button" type="primary" @click="ConcatConfirm()">确认</el-button>
          <el-button class="custom-button" @click="adddialogVisible = false">取消</el-button>
        </span>
      </el-dialog>
      <!-- 添加点表字段模态框 -->
      <el-dialog title="点表字段(或连接字段)"
        :visible.sync="dialogVisible"
        width='50%'
        >
        <div class="main">
          <vxe-button style="position: absolute;top: 3%;left:24%;width: 70px; height: 30px;" type="primary"  @click="addEvent1()" >添加</vxe-button>
          <vxe-table
            ref="xTable"
            :data="tableData2"
            keep-source
            :edit-config="{trigger: 'click', mode: 'cell',icon: 'fa fa-pencil', showStatus: true}">
            <vxe-table-column field="id" type="seq" title="序号" width="60"></vxe-table-column>
            <vxe-table-column field="name" title="名称" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="type" title="数据类型" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="description" title="描述" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
              <vxe-table-column field="choose" title="注释" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="optionList" title="可选项(以英文逗号分割)" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="defaultValue" title="默认值" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="unit" title="单位" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column  field="oper" title="操作">
                <template #default="{ row }">
                  <vxe-button type="text" icon="fa fa-trash-o" status="danger" content="删除" @click="delEvent1(row)"></vxe-button>
                </template>
            </vxe-table-column>
          </vxe-table>
        </div>
          <span slot="footer" class="dialog-footer">
            <el-button class="custom-button" type="primary" @click="orderConfirm()">确认</el-button>
            <el-button class="custom-button" @click="dialogVisible = false">取消</el-button>
          </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import js from '@/assets/js/main/CollectModuleManagement/index';
export default js;
</script>

<style lang="less" scoped>
.collectModule {
  height: calc(100vh - 90px);
  line-height: 100%;
  text-align: left;
  .header_con {
    width: 100%;
    height: 40px;
    background-color: #eff5ff;
  }
  .mian_con {
    padding: 0 10px;
  }
  .page_con {
    background-color: #f8f8f9;
    border: 1px solid #e8eaec;
    border-top: none;
  }
}
// @import '../../../assets/less/main/RoleManagement/index.less';
// @import '../../../assets/less/public/operationColumn.less';
</style>
